body {
    overflow: hidden;
    background: linear-gradient(rgb(180, 240, 255),rgb(83, 141, 131));
    background-repeat: no-repeat;
    height: 100vh;
}

.bat {
    width: 40rem;
    position: absolute;
    left: 50%;
    transform: translate(-50%);
    z-index: 1000;
}

.title {
    text-align: center;
    font-size: 4rem;   
}

.batpop {
    @extend .bat;
    z-index: 100;
}